import React from 'react';

export default class ToDoInput extends React.Component {
	render() {
		let time = new Date();
		const {title, desc, handleChange, handleSubmit} = this.props;
		return (
			<div className="my-3">
				<form onSubmit={handleSubmit}>
					<div className="input-group">
						<input type="text" className="form-control" placeholder="Add Subject" value={title} onChange={handleChange}/>
						<input type="hidden" value={time} />
					</div>
					<textarea className="w-100 mt-3" rows="10" placeholder="Description" value={desc} onChange={handleChange}></textarea>
					<button type="submit" className="btn btn-block btn-success">Add Item</button>
				</form>
			</div>
		);
	}
}
