import React from 'react';
import Draggable from 'react-draggable';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrash } from '@fortawesome/free-solid-svg-icons'

import { Accordion, Card, Button } from 'react-bootstrap';

export default class ToDoItem extends React.Component {
	render() {
		const { title, desc, color, handleDelete } = this.props;
		return (
			<Draggable  bounds="html"
				        axis="both"
				        handle=".draggableDiv"
				        defaultPosition={{x: 0, y: 0}}
				        position={null}
				        scale={1}
				        onStart={this.handleStart}
				        onDrag={this.handleDrag}
				        onStop={this.handleStop}>
				<div className="draggableDiv w-25">
				<Accordion defaultActiveKey="0">
					<li className="list-group-item text-capitalize d-flex justify-content-between p-1 cItems" style={{background: color}}>
						<h6>
							<Accordion.Toggle as={Button} variant="link" eventKey="0">
								<a data-toggle="collapse" href="#collapseExample" role="button" aria-expanded="false" aria-controls="collapseExample" className="white text-capitalize">
									{title}
								</a>
							</Accordion.Toggle>
							<Accordion.Collapse eventKey="0">
			                    <Card.Body>
			                    	<p className="justify-text">{desc}</p>
			                    </Card.Body>
			                </Accordion.Collapse>
						</h6>
						<div className="todo-icon">
							<span className="mx-2 text-danger close-btn" onClick={handleDelete}><FontAwesomeIcon icon={faTrash}></FontAwesomeIcon></span>
						</div>
					</li>
				</Accordion>
				</div>
			</Draggable>
		);
	}
}
